import React from 'react';
import './App.css';
import { LineChart, Line, XAxis, YAxis, Tooltip, Legend, CartesianGrid } from 'recharts';
import Clock from 'react-live-clock';

import config from "./config";
import { load } from "./api";


const temp = [];


class App extends React.Component {
  state = {
    data: temp,
    error: null
  }

  componentDidMount() {
    window.gapi.load("client", this.initClient);
    setInterval(this.initClient, 43200000);
  }

  initClient = () => {
    window.gapi.client
      .init({
        apiKey: config.apiKey,
        discoveryDocs: config.discoveryDocs
      })
      .then(() => {
      load(this.onLoad);
    });
  };

  onLoad = (data, error) => {
    if (data) {
      this.setState({ data });
    } else {
      this.setState({ error });
    }
  };

  renderColorfulLegendText = (value, entry) => {
  	const { color } = entry;
    
    return <span style={{ color }}>{value}</span>;
  };

  render() { 
    if (this.state.error) {
      return <div>{this.state.error}</div>;
    }
    if (this.state.data.length === 0) {
      return <div></div>;
    }
    return (
      <div className="App">
        <div class="box1">        
          <Clock className="clock" format={'h:mm A'} ticking={true} timezone={'US/Pacific'} />      
          <Clock className="date" format={'ddd, MMM Do'} ticking={true} timezone={'US/Pacific'} />
        </div>
        <div>
          <LineChart width={1000} height={300} data={this.state.data}
            margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
            <CartesianGrid strokeDasharray="5 5" />
            <XAxis dataKey="Date" />
            <YAxis yAxisId="left" domain={['auto', 'auto']} />
            <Tooltip />
            <Legend formatter={this.renderColorfulLegendText}/>
            <Line strokeWidth="3" yAxisId="left" type="monotone" dataKey="Income" stroke={config.color.blue} />
            <Line strokeWidth="3" yAxisId="left" type="monotone" dataKey="Spent" stroke={config.color.red} />
            <Line strokeWidth="3" yAxisId="left" type="monotone" dataKey="Cashflow" stroke={config.color.green} />
          </LineChart>
        </div>
      </div>
    );
  }
}

export default App;
